.Input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.Input label {
  margin-bottom: 12px;
  font-size: 1.1em;
}


.Input .input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  border-radius: 8px;
}

.Input input,
.Input button {
  font-size: 1.2em;
  padding: 6px;
  margin: 0;
}

.Input input {
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,0.2);
}

.Input button {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  background-color: #e5512f;
  color: white;
}


